.top-header {
  background-image: linear-gradient(
    to right,
    rgb(30, 89, 143),
    rgb(45, 119, 119)
  );
  height: 15vh;
  overflow: hideden;
}

.title-header {
  font-size: 3rem;
  text-align: center;
  overflow: hidden;
  padding-top: 4vh;
  font-weight: 700;
  color: white;
}

.search {
  width: 100%;
  height: 55vh;
}

.search-input {
  height: 100%;
  width: 90vw;
  float: left;
  margin-left: 3vw;
}

.search-result-group {
  margin-top: 5px;
  height: 45vh !important;
  width: 100%;
}

.search-button {
  height: 100%;
  width: auto;
  margin-left: 15px;
  font-size: 30px;
  text-align: center;
  background-color: lightgreen;
  border-color: white;
}

.search-form {
  width: 100%;
  padding: 5px;
}

.search-bar {
  padding: 15px;
}

.search-result {
  width: 100%;
}

.search-star {
  font-size: 20px;
  padding-left: 30px;
  padding-top: 5px;
}

.search-name {
  font-size: 1.5rem;
}

.search-content {
  font-size: 1.25rem;
}

.fa-star {
  font-size: 1.5rem !important;
}

.search-button-square {
  color: white;
  background-color: green;
  margin-left: 10px;
  height: 100%;
  font-size: 30px;
  border: none;
  border-radius: 5px;
  padding-bottom: 5px;
  width: 50px;
  -webkit-transform: scale(-1, 1);
  transform: scale(-1, 1);
}

.green {
  color: green !important;
}

.grey {
  color: lightgrey;
}

@media only screen and (max-width: 1000px) {
  .search-input {
    width: 85vw;
  }
}

@media only screen and (max-width: 780px) {
  .search-input {
    width: 80vw;
  }

  .search-name {
    font-size: 1rem;
  }

  .search-content {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 550px) {
  .title-header {
    font-size: 2rem;
    padding-top: 5vh;
  }
  .search-content {
    font-size: 0.75rem;
  }
}

@media only screen and (max-width: 420px) {
  .search-input {
    width: 75vw;
  }

  .search-content {
    font-size: 0.6rem;
  }

  .search-name {
    font-size: 0.75rem;
  }
}

.favorite {
  height: 30vh;
  background: rgba(173, 255, 47, 0.15);
}

.favorite-title {
  padding-top: 30px;
  padding-left: 30px;
  color: green;
}

.favorite-content-group {
  height: 71% !important;
}

@media only screen and (max-height: 800px) {
  .favorite-title {
    font-size: 2rem;
    padding-top: 10px;
  }
}

